.nav ul .active a .icon.icon_nav_2:before, .nav ul li a:active .icon.icon_nav_2:before { background-position: 0em -69.2em; }

.nav ul .active a .icon.icon_nav_3:before, .nav ul li a:active .icon.icon_nav_3:before { background-position: 0em -80.4em; }

.nav ul .active a .icon.icon_nav_4:before, .nav ul li a:active .icon.icon_nav_4:before { background-position: 0em -91.4em; }

