/*----------------------------------------------------------------------------*/
.btn { box-shadow: none; font-family: "Open Sans", sans-serif; display: inline-block; position: relative; font-weight: bold; text-align: center; line-height: 34px; padding: 0 20px; color: #fff; border-radius: 0px; border: none; overflow: hidden; text-decoration: none; backface-visibility: hidden; -moz-osx-font-smoothing: grayscale; height: 34px; background-color: #a6ce39; box-shadow: 2px 2px 0 0 #709114; font-weight: bold; font-size: 13px; }

