.t-inp, textarea { font-family: "Open Sans", sans-serif; padding: 8px 10px 8px 10px; text-overflow: ellipsis; overflow: hidden; display: block; width: 100%; height: 40px; color: #323232; border: solid 1px #9fb3c3; font-size: 13px; line-height: 20px; background: #fff; transition: all 0.3s linear; }

