.help_close:before, .help_close:after { top: 50%; left: 3px; margin-top: -1px; transition: all 0.3s linear; content: ''; display: block; width: 15px; height: 2px; background: #ffffff; position: absolute; transform: rotate(-45deg); }

.help_close:after { transform: rotate(45deg); }

.help_close:hover:before, .help_close:hover:after { background: #9fb3c3; }

.bg-pink { background: #ed1651; }

.bg-white { background: #fff; }

