.widg_table .td-hid:after { content: ''; position: absolute; top: 0; bottom: 0; right: 0; width: 30px; background-image: -webkit-linear-gradient(left, rgba(255, 255, 255, 0), #ffffff); background-image: -o-linear-gradient(left, rgba(255, 255, 255, 0), #ffffff); background-image: linear-gradient(to right, rgba(255, 255, 255, 0), #ffffff); }

